<p>Furthermore, Fiverr is not responsible for the content, quality or the level of service provided by the Sellers (even if they are Pro Sellers, Top Rated Sellers, offer Promoted Gigs or otherwise). We provide no warranty with respect to the Gigs, their delivery, any communications between Buyers and Sellers, and the Logo Designs created through the Logo Maker. We encourage users to take advantage of our rating system, our community and common sense in choosing appropriate services.</p>
